import styles from './app.module.scss';
import lottieMenu from 'assets/lottie/menu.json';
import { useState, useCallback } from 'react';
import { useLottie } from 'lottie-react';
import { HomeScreen } from 'screens/home/home.screen';
import { PortfolioScreen } from 'screens/portfolio/portfolio.screen';
import { C } from 'config/constants';
import { HobbyScreen } from 'screens/hobby/hobby.screen';
import {scrollTo} from 'utils/scrollTo';
import { Icon } from 'widgets';

export const App = () => {

  const [isMenuOpen, setMenuOpen] = useState(false);
  const {View, playSegments}  = useLottie({
    animationData: lottieMenu,
    autoplay: false,
  });

  const toSection = useCallback((section: any) => {
    playSegments(isMenuOpen ? [70, 0] : [0,70], true);
    setMenuOpen(prev => !prev);
    scrollTo(section);
  }, [isMenuOpen,playSegments]);

  return <>
    <nav className={isMenuOpen ? styles.menuOpen : ''}>
      <div>
        <div style={{width: '36px', height: '36px'}} onClick={() => {
          playSegments(isMenuOpen ? [70, 0] : [0,70], true);
          setMenuOpen(prev => !prev);
        }}>
          {View}
        </div>
      </div>
      {Object.keys(C.screens).map(k => <div key={k} onClick={() => toSection(k)}>{k}</div>)}
    </nav>
    <HomeScreen />
    <PortfolioScreen />
    <HobbyScreen />
    <div className={styles.footer}>
      <div>
        <Icon icon='copyleft' className={styles.copyleft} tint='#222' />
        <span>2022 Chang Liu</span>
      </div>
      <div>
        <a href="https://github.com/bobbyliu117" target='_blank' rel='noreferrer'>
          <Icon icon='github' className={styles.icon} tint='#222' />
        </a>
        <a href="https://twitter.com/changisadev" target='_blank' rel='noreferrer'>
          <Icon icon='twitter' className={styles.icon} tint='skyblue' />
        </a>
        <a href="https://www.linkedin.com/in/bobbychangliu/" target='_blank' rel='noreferrer'>
          <Icon icon='linkedin' className={styles.icon} tint='midnightblue' />
        </a>
      </div>
    </div>
  </>
}
